// 0. VueVueRouter Vue.use(Router)
import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

// 1. import views
import Login from './views/Login.vue';
import Register from './views/Register.vue';
import Index from './views/Index.vue';
import Category from './views/goods/Category.vue';
import GoodsList from './views/goods/GoodsList.vue';
import Release from './views/goods/Release.vue';
import Indent from './views/goods/Indent.vue';
import Client from './views/Client.vue';
import EditeGoodsList from './views/goods/EditeGoodsList.vue';
/**
 * 2. 
 * 
 * "component"  Vue.extend() 
 * 
 */
const routes = [{
		path: '/register',
		component: Register,
		name: 'Register',
	},
	{
		path: '/login/',
		component: Login,
		props: (route) => route.query,
		name: 'Login',
		alias: "/",
	},
	{
		path: '/index',
		component: Index,
		name: 'Index',
		children: [{
				path: '/goods/category',
				component: Category,
				name: 'Category',
				meta: {
					requiresAuth: true
				},
			}, {
				path: '/goods/list',
				component: GoodsList,
				name: 'GoodsList',
				meta: {
					requiresAuth: true
				},
			}, {
				path: '/goods/release',
				component: Release,
				name: 'Release',
				alias: "/",
				meta: {
					requiresAuth: true
				},
			}, {
				path: '/goods/edite-goods-list',
				component: EditeGoodsList,
				name: 'EditeGoodsList',
				alias: "/",
				meta: {
					requiresAuth: true
				},
			}, {
				path: '/goods/indent',
				component: Indent,
				name: 'Indent',
				meta: {
					requiresAuth: true
				},
			},
			{
				path: '/client',
				component: Client,
				name: 'Client',
				meta: {
					requiresAuth: true
				},
			}
		]
	},


]

// 3.  router  `routes` 
const router = new Router({
	routes // ()  routes: routes
})
// 4.
router.beforeEach((to, from, next) => {
	console.log(to);
	if (to.matched.some(record => record.meta.requiresAuth)) {
		// token
		if (!sessionStorage.token) {
			console.log('');
			next({
				path: "/login",
				// 
				query: {
					redirect: to.fullPath
				}
			});
			return;
		}
		// 
		next();
	} else {
		next();
	}

	console.log(from)
});
export default router
