import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import router from './router'
import {
	Message
} from 'element-ui'
Vue.use(Vuex)
let userModule = {
	state: {
		uid: '',
		role: '',
		token: '',
	},
	mutations: {
		saveUserInfo(state, user) {
			// 
			state.uid = user.uid;
			state.role = user.role;
			state.token = user.token;
		}
	},
	actions: {
		// 
		Login({
			commit,
			state
		}, FormData) {
			axios.post('/api/user/login', FormData).then((res) => {
				console.log(res)
				if (!res.status) {
					Message.error(res.msg);
					return false;
				}
				// token,uid,role(1-2-3-)
				sessionStorage.role = res.data.role;
				sessionStorage.token = res.data.token;
				sessionStorage.uid = res.data.id;
				// state
				commit('saveUserInfo', res.data);
				Message.success({
					message: res.msg,
					onClose: () => {
						// 
						if (this.redirect) {
							this.$router.push(this.redirectv);
							return;
						}
						// 
						router.push('/index')
					},
					type: 'success'
				});

			})
		}
	}
}
let menuModule = {
	state: {
		isCollapse: false,
	},
	mutations: {
		// 
		collapseMenu(state) {
			state.isCollapse = !state.isCollapse
		}

	},
	actions: {}
}
let wangEditorModule = {
	state: {
		editorContent: '',
		editeGood: '',
		editeDetail: ''

	},
	mutations: {
		// 
		increment(state, payload) {
			state.editorContent = payload.amount;
		},
		// state
		editeGoodMut(state, payload) {
			state.editeGood = payload.amount;
		},
		editeDetailMut(state, payload) {
			state.editeDetail = payload.amount
		}

	},
	actions: {}
}

export default new Vuex.Store({
	modules: {
		user: userModule,
		menu: menuModule,
		wangEditor: wangEditorModule,
	}
})
