<template>
	<el-container>
		<el-header>
			<Breadcrumb></Breadcrumb>
		</el-header>
		<el-main>
			<el-form ref="form" :model="form" :label-position="labelPosition" label-width="100px">
				<el-form-item label="">
					<el-select v-model="form.cate_1st" placeholder="" @change="cateChangeHandle_1st">
						<el-option v-for="item in dataCategory_lst[0]" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
					<el-select v-model="form.cate_2nd" placeholder="" @change="cateChangeHandle_2st">
						<el-option v-for="item in dataCategory_2st[0]" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
					<el-select v-model="form.cate_3rd" placeholder="">
						<el-option v-for="item in dataCategory_3st[0]" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="form.name"></el-input>
					<div class="text">3,300</div>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="form.hotPoint"></el-input>
					<div class="text">140</div>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="3">
						<el-input v-model="form.price" @blur="countDiscount">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24">
						<div class="text">0-999999<br></div>
					</el-col>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="3">
						<el-input v-model="form.marketPrice" @blur="countDiscount">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24">
						<div class="text">0-999999</div>
					</el-col>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="3">
						<el-input v-model="form.cost">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24">
						<div class="text">0-999999</div>
					</el-col>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="3">
						<el-input v-model="form.discount" disabled="disabled">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24">
						<div class="text"></div>
					</el-col>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="3">
						<el-input v-model="form.inventory">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24">
						<div class="text">0-999999<br></div>
					</el-col>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="4">
						<el-input v-model="form.articleNo">
						</el-input>
					</el-col>
					<el-col :span="24">
						<div class="text"><br>20_ / </div>
					</el-col>
				</el-form-item>
				</el-form-item>
				<el-form-item label="">
					<el-upload class="avatar-uploader" :headers="headers" action="/api/upload/goods/" :show-file-list="false"
					 :on-success="uploadSuccess" :before-upload="beforeUpload">
						<img width="100%" v-if="form.img_lg" :src="form.img_lg" class="avatar">
						<i v-else class="el-icon-plus avatar-uploader-icon"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">

					</el-dialog>
					<el-col :span="24">
						<div class="text">jpggifpng800*8001M,</div>
					</el-col>
				</el-form-item>
				<el-form-item label="">
					<el-upload class="avatar-uploader" :headers="headers" action="/api/upload/slider/" :show-file-list="true"
					 :on-success="listUploadSuccess" :before-upload="beforeUpload" list-type="picture-card">

						<i width="100%" class="el-icon-plus avatar-uploader-icon"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisibleList">
						<img width="100%" v-if="form.slider" :src="form.slider" class="avatar">
					</el-dialog>
					<el-col :span="24">
						<div class="text">jpggifpng800*8001M,</div>
					</el-col>
				</el-form-item>
				<div class="detail-bg"></div>
				<el-form-item label="">
					<el-col :span="17">
						<el-input v-model="form.input7">
						</el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="">
					<Editor></Editor>
				</el-form-item>
				<div class="detail-bg"></div>
				<el-form-item label="">
					<el-select v-model="form.region" placeholder="">
						<el-option label="" value="shanghai"></el-option>
						<el-option label="" value="beijing"></el-option>
					</el-select>
					<el-select v-model="form.region2" placeholder="">
						<el-option label="" value="shanghai"></el-option>
						<el-option label="" value="beijing"></el-option>
					</el-select>
					<el-select v-model="form.region3" placeholder="">
						<el-option label="" value="shanghai"></el-option>
						<el-option label="" value="beijing"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="3">
						<el-input placeholder="0" v-model="form.freight">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24">
						<div class="text">0,</div>
					</el-col>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="onSubmit"></el-button>
				</el-form-item>
			</el-form>
		</el-main>
	</el-container>

</template>

<script>
	import Editor from '@/components/Editor.vue';
	import Breadcrumb from '@/components/Breadcrumb.vue';
	import {
		mapState
	} from 'vuex';
	export default {
		components: {
			Editor,
			Breadcrumb
		},
		data() {
			return {
				labelPosition: 'left',
				dialogImageUrl: '',
				dialogVisible: false,
				dialogVisibleList: false,
				headers: {
					Authorization: `Bearer ${sessionStorage.token}`
				},
				form: {
					cate_1st: '',
					cate_2nd: '',
					cate_3rd: '',
					name: '',
					hotPoint: '',
					price: '',
					marketPrice: '',
					cost: '',
					discount: '',
					inventory: '',
					articleNo: '',
					img_lg: '',
					img_md: '',
					slider: '',
					brand: '',
					detail: '',
					freight: '',
					id: ''
				},
				dataCategory_lst: [],
				dataCategory_2st: [],
				dataCategory_3st: [],

			}
		},
		created() {
			this.Category_lst();
		},
		computed: mapState({
			// 
			editorContent: state => state.wangEditor.editorContent,

		}),
		methods: {
			onSubmit() {
				console.log('submit!');
				// storedata.form
				this.form.detail = this.editorContent;
				console.log(this.form);
				// 1.
				// 2.ajax
				this.$http.post('/api/goods/release', {
					...this.form
				}).then((res) => {
					console.log(res.msg);

				})
			},

			cateChangeHandle_1st() {
				// 
				this.form.cate_2nd = '';
				// 
				this.Category_2st();
			},
			cateChangeHandle_2st() {
				// 
				this.form.cate_3rd = '';
				// 
				this.Category_3st();

			},
			Category_lst() {
				// 
				this.dataCategory_lst = [];
				this.$http.get('/api/category/sub/', {
					params: {
						pId: 1
					}
				}).then((res) => {
					this.dataCategory_lst.push(res.data);
				});

			},
			Category_2st() {
				// 
				let id = this.form.cate_1st;
				// 
				this.dataCategory_2st = [];
				this.$http.get('/api/category/sub/', {
					params: {
						pId: id
					}
				}).then((res) => {
					this.dataCategory_2st.push(res.data);
				})
			},
			Category_3st() {
				// 
				let id = this.form.cate_2nd;
				// 
				this.dataCategory_3st = [];
				this.$http.get('/api/category/sub/', {
					params: {
						pId: id
					}
				}).then((res) => {
					this.dataCategory_3st.push(res.data);
				})
			},
			// 
			beforeUpload(file) {
				const isJPG = file.type === 'image/jpeg' || 'images/png';
				const isLt2M = file.size / 1024 / 1024 < 2;

				if (!isJPG) {
					this.$message.error(' JPG  PNG !');
				}
				if (!isLt2M) {
					this.$message.error(' 2MB!');
				}
				return isJPG && isLt2M;
			},
			// 
			uploadSuccess(res, file) {
				this.form.img_lg = res.lgImg;
				this.form.img_md = res.mdImg;
				// console.log(res)
			},
			listUploadSuccess(res, file) {
				this.form.slider += res.src;
				console.log(file)
				console.log(res)
			},
			countDiscount() {
				this.form.discount = this.form.price / this.form.marketPrice
			}
		}
	}
</script>

<style>
	.el-form-item__label {
		padding-right: 30px !important;
	}

	.detail-bg {
		background: #eee;
		padding: 1vw;
		margin-bottom: 1vw;
	}

	.el-button--primary {
		background: green;
		color: white;
	}

	.avatar-uploader .el-upload {
		border: 1px dashed #d9d9d9 !important;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}

	.avatar-uploader .el-upload:hover {
		border-color: #409EFF;
	}

	.avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 178px;
		height: 178px;
		line-height: 178px !important;
		text-align: center;
	}

	.avatar {
		width: 178px;
		height: 178px;
		display: block;
	}

	.avatar-uploader .el-upload {
		border: 1px dashed #d9d9d9 !important;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
		width: 178px;
		height: 178px;
		background-color: white;
	}

	.el-upload-list__item {
		width: 173px !important;
		height: 175px !important;
	}
</style>
